<template>
  <div id="app">
    <div v-show="loaded">
      <Menu/>
      <router-view/>
      <Footer/>
    </div>
  </div>
</template>

<script>
import Menu from "./components/_Menu";
import Footer from "./components/_Footer";
export default {
  name: "app",
  components: {
    Menu,
    Footer
  },
  data() {
    return {
      loaded: false
    };
  },
  watch: {
    $route(to, from) {
      AOS.refreshHard();
    }
  },
  methods: {},
  mounted() {
    addEventListener("load", () => {
      this.loaded = true;
    });
  }
};
</script>

